import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  List,
  Empty,
  Button,
  Input,
  Card,
  Pagination,
  Spin,
  Table,
  Popconfirm,
  Space,
} from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import {
  UploadOutlined,
  AppstoreOutlined,
  PlusOutlined,
  FilterOutlined,
} from "@ant-design/icons";
import PageTitle from "../../../../../layout/components/content/page-title";
import EmptyImage from "../../../../../assets/images/app/empty-list.svg";
import moment from "moment";
import {
  getInsuranceList,
  setInsuranceParams,
  isModelVisible,
  isDrawerVisible,
  isInsuranceViewModelVisible,
  getInsuranceById,
  clearInsuranceList,
  clearInsuranceParams,
} from "../../../../../store/sideMenu/newInsurance/newInsuranceSlice";
import { isMobile } from "react-device-detect";

import Filter from "./Filters/Filter";
import AddInsurance from "./addInsurance";
import InsuranceViewModel from "../InsuranceViewModel";
import AddVehicles from "../../../MastersDashboard/vehicles/addVehicles";
import * as Vehicles from "../../../../../store/Masters/Vehicles/vehiclesSlice";

function newInsurance(props) {
  const { Search } = Input;

  useEffect(() => {
    props.getInsuranceList({
      ...props.insuranceparams,
      page: 1,
      search: "",
      page_size: 10,
    });
    props.clearInsuranceList();
    props.clearInsuranceParams();
  }, []);

  function onPageChange(page, pageSize) {
    props.clearInsuranceList();
    props.clearInsuranceParams();
    props.setInsuranceParams({
      ...props.insuranceparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getInsuranceList({
      ...props.insuranceparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.setInsuranceParams({ ...props.insuranceparams, search: val });
    props.getInsuranceList({ ...props.insuranceparams, search: val });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  const data = props.newInsuranceList?.map((row, key) => ({
    ...row,
    // index: key + 1,
    index:
      (props.insuranceparams.page - 1) * props.insuranceparams.page_size +
      (key + 1),
  }));

  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";

  const columns = [
    {
      title: "S.No",
      width: 70,
      dataIndex: "index",
      fixed: isMobile ? null : "left",
    },
    {
      title: "Code",
      render: (row) => <span className={columnClass}>{row.code}</span>,
      width: 100,
      fixed: isMobile ? null : "left",
    },
    {
      title: "Date",
      width: 100,
      render: (row) => <span className={columnClass}>{row.date}</span>,
    },
    {
      title: "Customer Name",
      width: 100,
      render: (row) => <span className={columnClass}>{row.customername}</span>,
    },
    {
      title: "Customer Mobile",
      width: 110,
      render: (row) => (
        <span className={columnClass}>{row.customermobile}</span>
      ),
    },
    {
      title: "Aadhar Number",
      width: 130,
      render: (row) => <span className={columnClass}>{row.aadharno}</span>,
    },
    {
      title: "Counter Name",
      width: 120,
      render: (row) => <span className={columnClass}>{row.counter?.name}</span>,
    },
    {
      title: "Vehicle Number",
      width: 110,
      render: (row) => (
        <span className={columnClass}>
          {row.vehicle ? (
            <span className={columnClass}> {row.vehicle?.vehicleno} </span>
          ) : (
            none
          )}
        </span>
      ),
    },
    {
      title: "Customer Amount",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.customeramount}</span>
      ),
    },
    {
      title: "Requested Amount",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.requestedamount}</span>
      ),
    },
    {
      title: "Insurance Category",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.insurancecategory_name}</span>
      ),
    },
    {
      title: "Approved By",
      width: 120,
      render: (row) => (
        <span className={columnClass}>{row.reviewer?.fullname}</span>
      ),
    },
    {
      title: "Insurance Agent",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.insurer?.fullname}</span>
      ),
    },
    {
      title: "Actions",
      key: "action",
      width: 100,
      fixed: isMobile ? null : "right",
      render: (row, record) => (
        <Space size="middle">
          <>
            <i
              className="ri-eye-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
              onClick={() => {
                props.getInsuranceById(row.id);
                props.isInsuranceViewModelVisible(true);
              }}
            ></i>

            {props.userAccessList?.indexOf("InsuranceRto.change_insurance") !==
              -1 && (
              <>
                {row.insurancestatus == 1 && (
                  <>
                    <i
                      className="ri-edit-box-line hp-mr-2 "
                      style={{ cursor: "pointer", color: "#fc6f03" }}
                      onClick={() => {
                        props.getInsuranceById(row.id);
                        props.isModelVisible(true);
                      }}
                    ></i>
                  </>
                )}
              </>
            )}
          </>
        </Space>
      ),
    },
  ];

  return (
    <Row gutter={[12, 12]}>
      <PageTitle
        pageTitle="New Insurance"
        addModelTile2="Add Vehicle"
        setIsModalVisible2={props.vehicleIsModelVisible}
        isModalVisible2={props.model}
        goBack={false}
      />
      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          {/* <Col md={12} xs={24} className="hp-ecommerce-app-header-search "> */}
          <Col
            span={24}
            xs={24}
            sm={24}
            md={14}
            className="hp-ecommerce-app-header-search "
          >
            <Search
              placeholder="Search for date, doc, customer name, counter, vehicle, mobile and aadhar number "
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getInsuranceList({
                    ...props.insuranceparams,
                    page: 1,
                    search: "",
                  });
                  props.setInsuranceParams({
                    ...props.insuranceparams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>
          <Col span={24} xs={8} sm={8} md={3}>
            <Button
              onClick={() => {
                props.isDrawerVisible(true);
              }}
              className="hp-w-100 hp-p-12"
              icon={<FilterOutlined className="hp-mr-10" />}
            >
              {!isMobile && "Filter"}
            </Button>
          </Col>

          {props.userAccessList?.indexOf("InsuranceRto.add_insurance") !==
            -1 && (
            <Col span={24} xs={8} sm={8} md={4}>
              <Button
                onClick={() => {
                  props.isModelVisible(true);
                }}
                className="hp-w-100 hp-p-12"
                icon={<PlusOutlined className="hp-mr-10" />}
              >
                {!isMobile && "Add Insurance"}
              </Button>
            </Col>
          )}

          {props.userAccessList?.indexOf("InsuranceRto.export_insurance") !==
            -1 && (
            <Col span={24} xs={8} sm={8} md={3}>
              <Button
                className="hp-w-100 hp-p-12 "
                icon={<UploadOutlined className="hp-mr-10" />}
              >
                {!isMobile && "Export"}
              </Button>
            </Col>
          )}
        </Row>
      </Col>
      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  rowClassName="hp-btn-bg-color-none"
                  dataSource={data}
                  scroll={{ x: 1300 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.insuranceparams?.page}
                pageSize={props.insuranceparams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <InsuranceViewModel />
      <AddInsurance />
      <Filter />
      <AddVehicles />
    </Row>
  );
}
function mapStateToProps({ newInsurance, system }) {
  return {
    newInsuranceList: newInsurance.newInsuranceList,
    insuranceData: newInsurance.insuranceData,
    insuranceparams: newInsurance.insuranceparams,
    noofpages: newInsurance.noofpages,
    listCount: newInsurance.listCount,
    loading: newInsurance.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getInsuranceList,
  setInsuranceParams,
  isModelVisible,
  isDrawerVisible,
  isInsuranceViewModelVisible,
  getInsuranceById,
  vehicleIsModelVisible: Vehicles.isModelVisible,
  clearInsuranceList,
  clearInsuranceParams,
})(newInsurance);
