import { Fragment, useEffect } from "react";
import { connect } from "react-redux";
import { useHistory } from "react-router";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Row, Col, Select, Upload } from "antd";
import NumericInput from "../../../../components/data-entry/inputs/numberInput";
import {
  isModelVisible,
  InputChangeValue,
  getInsuranceImageUpload,
  addNewInsurance,
  getByVehicleVersion,
  getVersionInsuranceMaster,
} from "../../../../../store/sideMenu/newInsurance/newInsuranceSlice";
import {
  LoadingOutlined,
  PlusOutlined,
  EditOutlined,
  InfoCircleOutlined,
} from "@ant-design/icons";
import {
  getMiniVehicles,
  getMiniCounters,
  setMiniVehiclesParams
} from "../../../../../store/mini/miniSlice";

import {
  getVehicles,
  SetVehiclesParams,
} from "../../../../../store/Masters/Vehicles/vehiclesSlice";

const addInsurance = (props) => {
  const [form] = Form.useForm();
  const history = useHistory();
  useEffect(() => {
    props.getMiniVehicles({ ...props.vehiclesParams, page: 1 });
    props.getMiniCounters();
    props.getVehicles({ ...props.vehiclesParams, page: 1 });
  }, []);

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  const paidStatus = [
    { value: true, name: "Yes" },
    { value: false, name: "No" },
  ];

  const insuranceChoice = [
    { id: 1, name: "1st Party" },
    { id: 2, name: "2nd Party" },
    { id: 3, name: "3rd Party" },
  ];

  const beforeUpload = (file) => {
    const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";

    if (!isJpgOrPng) {
      message.error("You can only upload JPG/PNG file!");
    }

    const isLt2M = file.size / 1024 / 1024 < 2;

    if (!isLt2M) {
      message.error("Image must smaller than 2MB!");
    }

    return isJpgOrPng && isLt2M;
  };

  const uploadButton = (
    <>
      {" "}
      <div className="hp-center hp-mt-0 ">
        {props.imageLooading ? (
          <LoadingOutlined />
        ) : (
          <PlusOutlined className="hp-mt-16" />
        )}
        <div className="hp-mt-8 hp-mb-16">Upload</div>
      </div>
    </>
  );

  function getVehicleVersion(id) {
    props.miniVehiclesList.forEach((vehicle) => {
      if (vehicle.id == id) {
        props.getByVehicleVersion(vehicle.version);
      }
    });
  }

  // Adds New Country
  const handleAdd = () => {
    const insuranceObj = {
      ...props.insuranceData,
      id: 0,
    };
    props.addNewInsurance({ insuranceObj, history });
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const insuranceObj = {
      ...props.insuranceData,
    };
    props.addNewInsurance({ insuranceObj, history });
  };

  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.insuranceData.id == null || props.insuranceData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.insuranceData.id == null || props.insuranceData.id == 0
        ? "Add"
        : "Update"}{" "}
      Insurance
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      paid: props.insuranceData?.paid,
      customername: props.insuranceData?.customername,
      customermobile: props.insuranceData?.customermobile,
      aadharno: props.insuranceData?.aadharno,
      vehicle_id: props.insuranceData?.vehicle_id,
      counter_id: props.insuranceData?.counter_id,
      customeramount: props.insuranceData?.customeramount,
      requestedamount: props.insuranceData?.requestedamount,
      insurancecategory: props.insuranceData?.insurancecategory,
    });
  }, [props.insuranceData]);

  console.log("props.vehicleVersion", props.vehicleVersion);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={916}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          if (props.insuranceData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
      >
        <Row gutter={[12, 0]}>
          <Col md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Paid Status"
              name="paid"
              rules={[
                {
                  required: true,
                  message: "Please Select ",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Paid Status"
                optionFilterProp="paid"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "paid",
                    value: value,
                  });
                }}
                value={
                  props.insuranceData.paid == null
                    ? ""
                    : props.insuranceData?.paid
                }
              >
                {paidStatus.map((paid, index) => {
                  return (
                    <Option key={index} value={paid.value}>
                      {paid.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Counter"
              name="counter_id"
              rules={[
                {
                  required: true,
                  message: "Please Select Counter",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select a Counter"
                optionFilterProp="Counter"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  // props.GetCounterByID(value);
                  props.InputChangeValue({
                    key: "counter_id",
                    value: value,
                  });
                }}
                value={
                  props.insuranceData.counter_id == null
                    ? ""
                    : props.insuranceData?.counter_id
                }
              >
                {props.miniCountersList.map((counter, index) => {
                  return (
                    <Option key={index} value={counter.id}>
                      {counter.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>


        {props.insuranceData.id == null || props.insuranceData.id == 0 ? (
          <Col md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Vehicle" name="vehicle_id">
              <Select
                showSearch
                placeholder="Select Vehicle"
                optionFilterProp="vehicle_id"
                // filterOption={(input, option) =>
                //   option.children.toLowerCase().includes(input.toLowerCase())
                // }
                onSearch={(value) => {
                  props.setMiniVehiclesParams({
                    ...props.miniVehiclesParams,
                    page: 1,
                    search: value,
                  });
                  props.getMiniVehicles({
                    ...props.miniVehiclesParams,
                    page: 1,
                    search: value,
                  });
                }}
                defaultActiveFirstOption={false}
                showArrow={true}
                filterOption={false}
                onChange={(value) => {
                  getVehicleVersion(value);
                  props.getVersionInsuranceMaster(value);
                  props.InputChangeValue({
                    key: "vehicle_id",
                    value: value,
                  });
                }}
                onPopupScroll={() => {
                  if (
                    props.miniVehiclesParams.page < props.miniVehiclesParams.no_of_pages
                  ) {
                    props.setMiniVehiclesParams({
                      ...props.vehiclesParams,
                      page: props.miniVehiclesParams.page + 1,
                    });
                    props.getMiniVehicles({
                      ...props.miniVehiclesParams,
                      page: props.miniVehiclesParams.page + 1,
                    });
                  }
                }}
                // onPopupScroll={() => {
                //   if (
                //     props.vehiclesParams.page < props.vehiclesParams.no_of_pages
                //   ) {
                //     props.SetVehiclesParams({
                //       ...props.vehiclesParams,
                //       page: props.vehiclesParams.page + 1,
                //     });
                //     props.getVehicles({
                //       ...props.vehiclesParams,
                //       page: props.vehiclesParams.page + 1,
                //     });
                //   }
                // }}
                value={
                  props.insuranceData.vehicle_id == null
                    ? ""
                    : props.insuranceData?.vehicle_id
                }
              >
                {props.miniVehiclesList.map((vehicle, index) => {
                  return (
                    <Option key={index} value={vehicle.id}>
                      {vehicle.vehicleno}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

        ) : (

          <Col  md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
              <Form.Item label="Vehicle Number">
                <Input
                  id="vehicleno"
                  name="vehicleno"
                  style={{ width: "100%" }}
                  placeholder="Vehicle Number"
                  value={props.insuranceData?.vehicle?.vehicleno}
                  disabled
                />
              </Form.Item>
            </Col>
          )}

          <Col md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Vehicle Version">
              <Input
                id="vehicleVersion"
                name="vehicleVersion"
                style={{ width: "100%" }}
                placeholder="Vehicle Version"
                // value={props.insuranceData?.vehicle?.version?.name}
                value={
                  props.insuranceData. id == 0 || props.insuranceData.id== null
                    ? props.vehicleVersion?.name
                    : props.insuranceData?.vehicle?.version?.name
                }
                disabled
              />
            </Form.Item>
          </Col>

          <Col md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Insurance Category"
              name="insurancecategory"
              tooltip={{
                title:
                  "First-Party:" +
                  props.versionInsurance?.firstparty +
                  ", " +
                  "Second-Party:" +
                  props.versionInsurance?.secondparty +
                  ", " +
                  "Third-Party:" +
                  props.versionInsurance?.thirdparty,

                icon: <InfoCircleOutlined />,
              }}
              rules={[
                {
                  required: true,
                  message: "Please Select Insurance Category",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                disabled={
                  props.insuranceData?.vehicle_id == null ? true : false
                }
                placeholder="Insurance Category"
                optionFilterProp="insurancecategory"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "insurancecategory",
                    value: value,
                  });
                }}
                value={
                  props.insuranceData.insurancecategory == null
                    ? ""
                    : props.insuranceData?.insurancecategory
                }
              >
                {insuranceChoice.map((insurance, index) => {
                  return (
                    <Option key={index} value={insurance.id}>
                      {insurance.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Customer Name"
              name="customername"
              rules={[
                {
                  required: true,
                  message: "Please Input Customer Name",
                },
                {
                  pattern: new RegExp(/^[a-zA-Z][a-zA-Z\s]+$/i),
                  message: "field does not accept numbers",
                },
              ]}
              hasFeedback
            >
              <Input
                id="customername"
                name="customername"
                style={{ width: "100%" }}
                maxLength={20}
                placeholder="Customer Name"
                value={props.insuranceData.customername}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>

          <Col md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Customer Mobile"
              name="customermobile"
              rules={[
                {
                  required: true,
                  message: "Please Input Customer Mobile",
                },
                {
                  min: 10,
                  message: "Please Input Customer Mobile",
                },
              ]}
              hasFeedback
            >
              <NumericInput
                id="customermobile"
                name="customermobile"
                style={{ width: "100%" }}
                placeholder="Customer Mobile"
                maxLength={10}
                value={
                  props.insuranceData?.customermobile == null
                    ? ""
                    : props.insuranceData?.customermobile
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Aadhar Number"
              name="aadharno"
              rules={[
                {
                  required: true,
                  message: "Please Input Aadhar Number",
                },
                {
                  min: 12,
                  message: "Please Input Aadhar Number",
                },
              ]}
              hasFeedback
            >
              <NumericInput
                id="aadharno"
                name="aadharno"
                style={{ width: "100%" }}
                placeholder="Aadhar Number"
                maxLength={12}
                value={
                  props.insuranceData?.aadharno == null
                    ? ""
                    : props.insuranceData?.aadharno
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Customer Amount"
              name="customeramount"
              rules={[
                {
                  required: true,
                  message: "Please Input Customer Amount",
                },
              ]}
              hasFeedback
            >
              <NumericInput
                id="customeramount"
                name="customeramount"
                style={{ width: "100%" }}
                placeholder="Customer Amount"
                maxLength={6}
                value={
                  props.insuranceData?.customeramount == null
                    ? ""
                    : props.insuranceData?.customeramount
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={6} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Requested Amount"
              name="requestedamount"
              rules={[
                {
                  required: true,
                  message: "Please Input Requested Amount",
                },
              ]}
              hasFeedback
            >
              <NumericInput
                id="requestedamount"
                name="requestedamount"
                style={{ width: "100%" }}
                placeholder="Requested Amount"
                maxLength={6}
                value={
                  props.insuranceData?.requestedamount == null
                    ? ""
                    : props.insuranceData?.requestedamount
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={[12, 0]}>
          <Col span={24} xs={24} xl={6} className="hp-pr-sm-0 hp-pr-12">
            <p className="hp-pb-xs-16 hp-mb-2 hp-text-color-primary-1 hp-font-weight-600">
              Aadhar Front Image
            </p>
            <Upload
              listType="picture-card hp-w-100"
              className="avatar-uploader"
              showUploadList={false}
              accept="image/*"
              beforeUpload={beforeUpload}
              customRequest={(info) => {
                props.getInsuranceImageUpload({
                  file: info.file,
                  imageType: "aadharfront_image",
                });
              }}
            >
              {props.insuranceData.aadharfront_image != null ? (
                <div className="hp-d-flex">
                  <img
                    className="ant-upload-select-picture-card hp-preview-img"
                    src={props.insuranceData.aadharfront_image?.file}
                    alt="avatar"
                    style={{
                      width: "100%",
                    }}
                  />
                  <EditOutlined />
                </div>
              ) : (
                uploadButton
              )}
            </Upload>
          </Col>

          <Col span={24} xs={24} xl={6} className="hp-pr-sm-0 hp-pr-12">
            <p className="hp-pb-xs-16 hp-mb-2 hp-text-color-primary-1 hp-font-weight-600">
              Aadhar Back Image
            </p>
            <Upload
              listType="picture-card hp-w-100"
              className="avatar-uploader"
              showUploadList={false}
              accept="image/*"
              beforeUpload={beforeUpload}
              customRequest={(info) => {
                props.getInsuranceImageUpload({
                  file: info.file,
                  imageType: "aadharback_image",
                });
              }}
            >
              {props.insuranceData.aadharback_image != null ? (
                <div className="hp-d-flex">
                  <img
                    className="ant-upload-select-picture-card hp-preview-img"
                    src={props.insuranceData.aadharback_image?.file}
                    alt="avatar"
                    style={{
                      width: "100%",
                    }}
                  />
                  <EditOutlined />
                </div>
              ) : (
                uploadButton
              )}
            </Upload>
          </Col>

          <Col span={24} xs={24} xl={6} className="hp-pr-sm-0 hp-pr-12">
            <p className="hp-pb-xs-16 hp-mb-2 hp-text-color-primary-1 hp-font-weight-600">
              C Book Image
            </p>
            <Upload
              listType="picture-card hp-w-100"
              className="avatar-uploader"
              showUploadList={false}
              accept="image/*"
              beforeUpload={beforeUpload}
              customRequest={(info) => {
                props.getInsuranceImageUpload({
                  file: info.file,
                  imageType: "cbook_image",
                });
              }}
            >
              {props.insuranceData.cbook_image != null ? (
                <div className="hp-d-flex">
                  <img
                    className="ant-upload-select-picture-card hp-preview-img"
                    src={props.insuranceData.cbook_image?.file}
                    alt="avatar"
                    style={{
                      width: "100%",
                    }}
                  />
                  <EditOutlined />
                </div>
              ) : (
                uploadButton
              )}
            </Upload>
          </Col>

          <Col span={24} xs={24} xl={6} className="hp-pr-sm-0 hp-pr-12">
            <p className="hp-pb-xs-16 hp-mb-2 hp-text-color-primary-1 hp-font-weight-600">
              Old Policy Image
            </p>
            <Upload
              listType="picture-card hp-w-100"
              className="avatar-uploader"
              showUploadList={false}
              accept="image/*"
              beforeUpload={beforeUpload}
              customRequest={(info) => {
                props.getInsuranceImageUpload({
                  file: info.file,
                  imageType: "oldpolicy_image",
                });
              }}
            >
              {props.insuranceData.oldpolicy_image != null ? (
                <div className="hp-d-flex">
                  <img
                    className="ant-upload-select-picture-card hp-preview-img"
                    src={props.insuranceData.oldpolicy_image?.file}
                    alt="avatar"
                    style={{
                      width: "100%",
                    }}
                  />
                  <EditOutlined />
                </div>
              ) : (
                uploadButton
              )}
            </Upload>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ newInsurance, mini, vehicles }) {
  return {
    insuranceData: newInsurance.insuranceData,
    model: newInsurance.model,
    miniCountersList: mini.miniCountersList,
    miniVehiclesList: mini.miniVehiclesList,
    vehiclesParams: vehicles.vehiclesParams,
    vehicleVersion: newInsurance.vehicleVersion,
    versionInsurance: newInsurance.versionInsurance,
    miniVehiclesParams: mini.miniVehiclesParams
  };
}

export default connect(mapStateToProps, {
  isModelVisible,
  InputChangeValue,
  getMiniVehicles,
  SetVehiclesParams,
  getVehicles,
  getInsuranceImageUpload,
  getMiniCounters,
  addNewInsurance,
  getByVehicleVersion,
  getVersionInsuranceMaster,
  setMiniVehiclesParams
})(addInsurance);
